    if(timer){         //å…ˆåˆ¤æ–­æœ‰æ²¡æœ‰è®¡æ—¶å™¨ï¼Œæœ‰çš„è¯å°±æ¸…é™¤ï¼Œæ²¡æœ‰çš„è¯å°±è®¾ç½®ä¸€ä¸ª
        clearInterval(timer)
    }
